<template>
  <section class="deposits-tab">
    <Text
      variant="p"
      size="12"
      weight="700"
      mb="12px"
      uppercase
    >
      {{ t('deposits.selectCrypto') }}
    </Text>

    <div class="deposits-tab__main">
      <div class="deposits-tab__select">
        <CryptoBlock
          v-for="item in cryptoList"
          :key="item.key"
          :active="selectedCrypto?.key === item.key"
          :crypto="item.key"
          class="deposits-tab__select-item"
          @click="selectCrypto(item)"
        />
      </div>
      <Container background-color="#FAFAFA" class="deposits-tab__crd">
        <Text
          variant="p"
          theme="primary"
          size="12px"
          mb="20px"
        >{{ t('depositPage.scan') }}</Text>
        <div>
          <QrCode width="204" height="201" />
        </div>
        <OrDivider align="horizontal"/>
        <Text
          variant="p"
          theme="primary"
          size="12px"
          mb="12px"
        >{{ t('depositPage.send') }}</Text>

        <Input
          v-model="legacy"
          hide-shadow
          :label="t('deposits.legacy')"
          class="deposits-tab__crd-legacy"
        >
          <template #append>
            <Icon name="copy" @click="copy(legacy)" />
          </template>
        </Input>

      </Container>
    </div>
  </section>
</template>

<script>
import { useI18n } from '@/shared/plugins/i18n'
import CryptoBlock from '@/admin/views/treasury/Deposits/CryptoBlock'
import Text from '@/shared/components/UIKit/Text/Text'
import Container from '@/shared/components/Container'
import { reactive, ref } from 'vue'
import Input from '@/shared/components/UIKit/Input'
import Icon from '@/shared/components/Icon/Icon'
import { copy } from '@/shared/utils/copyToClipboard'
import OrDivider from '@/shared/components/OrDivider'
import QrCode from '@/shared/components/QrCode'

export default {
  name: 'DepositTab',
  components: { QrCode, OrDivider, Icon, Input, Container, Text, CryptoBlock },
  setup () {
    const { t, locale } = useI18n()

    const cryptoList = reactive([
      { key: 'btc' },
      { key: 'ltc' },
      { key: 'xmr' },
      { key: 'eth' },
      { key: 'usdt' },
      { key: 'pm' }
    ])
    const selectedCrypto = ref(null)
    const legacy = ref('MQi2aB3HbFoNkntUyMQi2aBMQi2aB3HbFoN')

    const selectCrypto = (item) => {
      selectedCrypto.value = item
    }

    return {
      t,
      locale,
      legacy,
      cryptoList,
      selectedCrypto,
      selectCrypto,
      copy
    }
  }
}
</script>

